// All Styles
.seeds-block-paragraph-item {
  .field--name-field-seeds-link {
    margin-top: 15px;
    padding: 0;

    a {
      padding: 6px 12px;
      display: inline-block;
      color: white;

      &:hover,
      &:active,
      &:focus {
        text-decoration: none;
      }
    }
  }

  .field--name-field-seeds-image {
    position: relative;
    img {
      width: 100%;
    }
  }
}

.block-card {
  position: relative;
  .field--name-field-seeds-image {
    &::after {
      content: "";
      background: rgba(0, 0, 0, 0.2);
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      position: absolute;
    }
  }
  .body-wrapper {
    z-index: 1;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0 15px 30px 15px;

    .field--name-field-seeds-title,
    .field--name-field-seeds-text {
      color: white;

      h2 {
        color: white;
      }
    }
  }
}

.block-teaser {
  .field {
    margin-bottom: 10px;

    &.field--name-field-seeds-link {
      margin-top: 0px;
    }
  }
}

.block-teaser-right {
  flex-direction: row-reverse;
}

.block-content--type-seeds-accordion {
  .block-teaser-right,
  .block-teaser-left {
    .wrapper {
      display: flex;
      flex-wrap: wrap;

      .field--name-field-seeds-image,
      .body-wrapper {
        flex: 0;
        flex-basis: 50%;
      }

      .body-wrapper {
        padding: 20px;
      }

      @media (max-width: 767px) {
        .field--name-field-seeds-image,
        .body-wrapper {
          flex-basis: 100%;
        }
      }
    }
  }
}

.block-content:not(.block-content--type-seeds-accordion) {
  .block-teaser-right,
  .block-teaser-left {
    display: flex;
    flex-wrap: wrap;

    .field--name-field-seeds-image,
    .body-wrapper {
      flex: 0;
      flex-basis: 50%;
    }

    .body-wrapper {
      padding: 20px;

      .field--name-field-seeds-title {
        h2 {
          margin: 0 0 5px 0;
        }
      }
    }

    @media (max-width: 767px) {
      .field--name-field-seeds-image,
      .body-wrapper {
        flex-basis: 100%;
      }
    }
  }
}
